.event-sub-category,
.event-sub-category:hover {
  font-weight: 400;
  color: #9e9e9e;
  cursor: pointer;
  white-space: nowrap;
  text-decoration: none;
  margin-right: 52px;
  font-size: 17px;
  font-family: Mulish, Helvetica, Arial, sans-serif !important;
}

.event-sub-category-selected,
.event-sub-category-selected:hover {
  color: #fa7328;
  font-weight: 700;
}

.event-sub-category-list-container {
  overflow: auto;
  white-space: nowrap;
  background: #fafafa;
  padding: 6px 24px;
  margin-bottom: 34px;
  /* display: flex; */
}
