.category-tag {
  font-size: 14px;
  padding: 6px 12px;
  font-weight: 400;
  border-radius: 2px;
  margin-right: 5px;
  margin-bottom: 10px;
  cursor: pointer;
  text-decoration: none;
  color: #424242;
  background: #eee;
}

.category-tag-selected {
  color: #fff;
  background: #fa7328;
}
