.paging-cotnaienr {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
  align-items: center;
}

.nav-arrow-container {
  background: #fc9d69;
  box-shadow: 0 2px 4px rgba(24, 4, 50, 0.24);
  border-radius: 4px;
  padding: 12px 18px;
  cursor: pointer;
  -ms-user-select: none;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-user-drag: none;
}

.arrow-disabled {
  cursor: not-allowed;
  pointer-events: none;
  box-sizing: border-box;
  opacity: 0.5;
}

.page-number-container {
  margin: 0 14px;
  color: #616161;
  font-size: 16px;
  font-weight: 600;
}

.page-number-input {
  -moz-appearance: textfield;
  border: 1px solid #d0d0d0;
  color: #616161;
  font-size: 16px;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 8px;
  text-align: center;
  width: 50px;
  background: #fff;
  margin: 0 6px;
}

@media (max-width: 768px) {
  .paging-cotnaienr {
    justify-content: center;
  }
}
