.nav-elements-c,
.nav-elements-c:hover {
  font-size: 19px;
  padding-right: 84px;
  display: flex;
  align-items: center;
  font-family: Mulish, Helvetica, Arial, sans-serif;
  font-weight: 400;
  color: #9e9e9e;
  cursor: pointer;
  white-space: nowrap;
  text-decoration: none;
}

.nav-elements-img {
  margin-right: 8px;
  width: 20px;
}
.nav-elements-container {
  /* display: inline-block; */
  overflow-x: auto;
}

.events-bar {
  padding: 20px 18px;
  overflow: auto;
  white-space: nowrap;
}
.event-category-selected,
.event-category-selected:hover {
  color: #fa7328;
  font-weight: 700;
}
