.event-card-container {
  margin: 1rem;
  font-family: Mulish, Helvetica, Arial, sans-serif !important;
  max-width: 40%;
}

@media (max-width: 768px) {
  .event-card-container {
    margin: auto;
    min-width: 100%;
  }
}

.card-title-c {
  text-align: left;
  margin: 0 0 9px;
  font-size: 18px;
  letter-spacing: 0.2px;
  font-weight: 600;
  min-height: 50px;
}
.card-body-c {
  padding: 20px 16px;
}
.card-badge-c {
  text-align: left;
  margin: 0 2px;
  height: fit-content;
  margin-right: 8px;
  margin-bottom: 8px;
  color: #616161;
  background: #eee;
  border-radius: 3px;
  padding: 4px 14px;
  font-size: 12px;
}
.card-button-c {
  background: linear-gradient(90deg, #fa7328, #ee9f15);
  border-radius: 3px;
  color: #fff;
  width: 132px;
  height: 32px;
  border: none;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
}

.card-text-c {
  text-align: left;
  margin: 12px 0px 0px;
}

.event-details {
  display: flex;
  text-align: left;
}

@media (max-width: 768px) {
  .event-details {
    font-size: 11px !important;
  }
}
.card-details-label {
  color: #757575;
  font-weight: 400;
  margin-right: 5px;
  margin-bottom: 0;
}

.card-details-label-value {
  color: #212121;
  font-weight: 600;
  font-size: 14px;
}

.event-details-container {
  display: flex;
  flex-direction: column;
  margin-right: 16px;
}

.card-badge-container {
  display: flex;
  flex-wrap: wrap;
  height: 64px;
  margin: 20px 0 15px 0;
}
.img-properties-c {
  border-radius: 50%;
}
.row-img-c {
  display: flex;
}

.column-img-c {
  flex: 33.33%;
  padding: 5px;
}
.footer-row-img {
  display: flex;
}

.footer-col-img {
  flex: 20%;
  padding: 5px;
}
.rounded {
  border-radius: 50%;
}
.card-footer-container {
  display: flex;
  justify-content: space-between;
}
.card-participant-img {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 4px;
}
.card-participants {
  width: 35%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: left;
}
.participant-img {
  border: 1px solid #fff;
  width: 25px;
  height: 25px;
  border-radius: 70%;
  vertical-align: middle;
  margin: 0 1px;
}
.participant-data {
  margin: 0;
  color: #757575;
  font-size: 13px;
}
