.events-container {
  font-weight: 400;
  border: 1px solid #d0d0d0;
  background: #fff;
  border-radius: 6px;
  padding: 70px 130px;
}
.events-tags-container {
  width: 100%;
  padding: 70px 130px;
  /* margin: 24px; */
  display: flex;
  /* justify-content: center; */
}

.events-cards-list-outer-container {
  width: 80%;
}

.events-tags-outer-container {
  width: 20%;
}

@media (max-width: 768px) {
  .events-cards-list-outer-container {
    width: 90%;
  }
  .events-tags-outer-container {
    display: none;
  }
}
